import cookie from '@/cookie'

class User {
  constructor (name, role, group) {
    this.name = name
    this.role = role
    this.group = group
  }
}

export default {
  state: {
    user: null
  },
  mutations: {
    authUser (state, payload) {
      state.user = payload
    }
  },
  actions: {
    isUserAuth ({ commit }) {
      if (cookie.getCookie('user') !== undefined) {
        const { name, role, group } = cookie.getCookie('user')
        commit('authUser', new User(name, role, group))
        return true
      } else {
        return false
      }
    },
    async authUser ({ commit }, payload) {
      // делаем запрос к бд с login/password
      commit('clearError')
      commit('setLoading', true)
      return await new Promise((resolve, reject) => {
        setTimeout(() => {
          if (payload.login === 'skf-mtusi' && payload.password === 'skf-mtusi') {
            commit('setLoading', false)
            const value = JSON.stringify(new User('Фамилия Имя Отчество', 'student', 'ДИ-11'))
            cookie.setCookie('user', value)
            commit('authUser', new User('Фамилия Имя Отчество', 'student', 'ДИ-11'))
            resolve('Success auth')
          } else {
            commit('setLoading', false)
            commit('setError', 'Пользователь не найден')
            const e = 'Пользователь не найден'
            reject(e)
          }
        }, 2000)
      })
    }
  },
  getters: {
    user: state => state.user
  }
}
